
.single_post_wrap .single_post .singlepost .content img{max-width:100%;width:auto;height:auto
}
.single_post_wrap .single_post .singlepost .content p{font:16px/24px "PTR", Helvetica, Arial, sans-serif;color:#000;margin:16px 0
}
.single_post_wrap .single_post .singlepost .content p em{font-family:"PTI", Helvetica, Arial, sans-serif;font-style:normal
}
.single_post_wrap .single_post .singlepost .content hr{margin:20px 0;border:none;height:1px;padding:20px 0;position:relative;background:transparent url(../../assets/paragraph.png) no-repeat top center;background-size:30px 36px
}
.single_post_wrap .single_post .singlepost .content hr:after,.single_post_wrap .single_post .singlepost .content hr:before{content:'';width:30%;position:absolute;top:15px;border:1px solid #d8d8d8
}
.single_post_wrap .single_post .singlepost .content hr:after{left:10%
}
.single_post_wrap .single_post .singlepost .content hr:before{right:10%
}
.single_post_wrap .single_post .singlepost .content a{text-decoration:none;color:#000;position:relative;cursor:pointer;transition:color 0.1s;background:transparent linear-gradient(#b88b58 50%, transparent 50%) repeat-x 0 100%/1em 2px
}
.single_post_wrap .single_post .singlepost .content a:hover{color:#b88b58
}
.single_post_wrap .single_post .singlepost .content ul{margin:16px 0;padding:0;list-style-type:none
}
.single_post_wrap .single_post .singlepost .content ul li{font:16px/24px "PTR", Helvetica, Arial, sans-serif;color:#000;padding-left:30px;position:relative;margin:0 0 10px
}
.single_post_wrap .single_post .singlepost .content ul li:before{content:'\2022';position:absolute;left:5px;top:0;width:8px;text-align:center;color:#b88b58;font-size:16px;line-height:inherit
}
.single_post_wrap .single_post .singlepost .content div.wp-caption{max-width:100%
}
.single_post_wrap .single_post .singlepost .content div.wp-caption p.wp-caption-text{margin:0;color:#666;font-size:13px;line-height:normal;text-align:center
}



