
.posts{width:100%;max-width:1170px;padding:0 15px;margin:0 auto;padding-bottom:100px;padding-top:60px
}
.posts .post-wrap{display:block;width:550px;margin-bottom:50px
}
.posts .post-wrap:nth-child(odd){float:left
}
.posts .post-wrap:nth-child(even){float:right
}
.posts .post-wrap .img_wrap{width:100%;height:300px;display:inline-block;position:relative;overflow:hidden;vertical-align:top
}
.posts .post-wrap .img_wrap img{position:absolute;width:100%;height:auto;transform:translateY(-50%);top:50%;left:0;min-height:100%
}
.posts .post-wrap .img_wrap .icon_wrap{width:40px;height:40px;background-color:#b88b58;position:absolute;right:430px;bottom:0
}
.posts .post-wrap .img_wrap .icon_wrap i{color:#fff;font-size:23px;margin-top:8px
}
.posts .post-wrap .img_wrap .title{padding:0 15px;height:55px;line-height:55px;font-weight:bold;text-transform:uppercase;text-align:right;position:absolute;bottom:0;right:0;width:400px;background-color:#fff;color:#000;font-size:16px;text-overflow:ellipsis;white-space:nowrap;overflow:hidden
}
.posts .post-wrap .date{width:120px;float:left;text-align:center;padding:20px 0;color:#7f7f7f
}
.posts .post-wrap .descr{padding:15px 15px 0 15px;text-align:left;height:120px;overflow:hidden;max-width:400px;float:right;font-size:18px;line-height:28px;color:#7f7f7f
}
.posts .post-wrap .descr img{width:100%
}
.posts .post-wrap .button_wrap{float:right;width:430px;text-align:left
}
.posts .post-wrap .button_wrap button{width:100px;height:35px;border:2px solid #b88b58;font-size:12px;background-color:#fff;text-transform:uppercase;font-weight:bold;margin:10px 0 0 15px;transition:all 0.17s;cursor:pointer !important
}
.posts .post-wrap .button_wrap button:hover{background-color:#b88b58;color:#fff
}
.posts .post-wrap .button_wrap button:active,.posts .post-wrap .button_wrap button:focus{outline:none
}
.posts .load_more{display:block;height:30px;width:140px;margin:0 auto;margin-bottom:130px;cursor:pointer
}
.posts .clear{clear:both
}
@media only screen and (max-width: 1200px){
.posts{width:calc(100% - 30px);padding:40px 15px
}
.posts .post-wrap{width:100%;max-width:550px;margin:0 auto 50px auto
}
.posts .post-wrap:nth-child(odd){float:none
}
.posts .post-wrap:nth-child(even){float:none
}
}
@media only screen and (max-width: 768px){
.posts .post-wrap .img_wrap{width:100%;height:300px;display:inline-block;position:relative;overflow:hidden;vertical-align:top
}
.posts .post-wrap .img_wrap .icon_wrap{width:30px;height:30px;background-color:#b88b58;right:calc(70% + 30px)
}
.posts .post-wrap .img_wrap .icon_wrap i{font-size:15px;margin-top:7px
}
.posts .post-wrap .img_wrap .title{height:40px;line-height:40px;width:70%;font-size:14px
}
.posts .post-wrap .date{width:calc(30% - 30px)
}
.posts .post-wrap .descr{width:70%;font-size:18px;line-height:28px
}
.posts .post-wrap .button_wrap{float:none;width:100%;position:relative
}
.posts .post-wrap .button_wrap button{position:absolute;top:-60px;left:0;margin:10px 0 0 0
}
}
@media only screen and (max-width: 480px){
.posts .post-wrap{display:block;margin:0 auto 20px auto
}
.posts .post-wrap .img_wrap{height:200px
}
.posts .post-wrap .descr{font-size:16px;line-height:24px;height:95px
}
.posts .post-wrap .button_wrap{text-align:center
}
.posts .post-wrap .button_wrap button{position:relative;top:0;left:0;margin:20px 0 0 0
}
}



